/* Social Icons */
#Social-Widgets{
    margin-right: 2%;
}
#Social-Widgets img{
    -webkit-animation: socialWidgetsInit 1s;
            animation: socialWidgetsInit 1s;
    height: 40px;
    transition: 500ms;
    color: blue;
    margin-left: 10px;
    margin-right: 2px;
    border-radius: 10px;
}
#Social-Widgets img:hover{
    transition: 500ms;
    opacity: 0.3;
