/* DROPDOWN MENU */

#Dropdown h1{
    transition: 200ms;
    color: rgb(0, 0, 0);
    background-color: #ffffff;
    border-radius: 10px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    cursor: default;
}
#Dropdown img{
    -webkit-animation: floatMenu 1s ease-in-out infinite;
            animation: floatMenu 1s ease-in-out infinite;
}
#Dropdown h1:hover{
    transition: 200ms;
    color: rgb(119, 119, 119);
    background-color: #cecece;
    border-radius: 10px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

#Dropdown {
    
    display: inline-block;
    
}

.Dropdown-content {
    /* transition: 1000ms; */
    /* display: none; */
    /* opacity: 0; */
    -webkit-animation: hideDropdownMenu 1s;
            animation: hideDropdownMenu 1s;
    margin-left: -100%;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    position: absolute;
    background-color: aliceblue;
    min-width: 160px;
    box-shadow: 8px 8px 10px 2px rgb(0, 0, 0);
    border-radius: 15px;
}
.Dropdown-content a{
    transition: 500ms;
    color: black;
    text-align: left;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
}
.Dropdown-content a:hover{
    transition: 500ms;
    color: rgb(122, 122, 122);
    -webkit-animation: floatMenuLink 200ms ease-in-out;
            animation: floatMenuLink 200ms ease-in-out;
    -webkit-transform: translatex(5px);
            transform: translatex(5px);

}

#Dropdown:hover .Dropdown-content{
    opacity: 1;
    display: block;
    -webkit-animation: showDropdownMenu 0.5s;
            animation: showDropdownMenu 0.5s;
    margin-left: 0%;


}


@-webkit-keyframes showDropdownMenu{
    from {
        margin-left: -100%;
    }

    to {
        margin-left: 0%;
        opacity: 1;
    }
}


@keyframes showDropdownMenu{
    from {
        margin-left: -100%;
    }

    to {
        margin-left: 0%;
        opacity: 1;
    }
}

@-webkit-keyframes hideDropdownMenu{
    from {
        margin-left: 0%;
    }

    to {
        margin-left: -100%;
        opacity: 0;
    }
}

@keyframes hideDropdownMenu{
    from {
        margin-left: 0%;
    }

    to {
        margin-left: -100%;
        opacity: 0;
    }
}

@-webkit-keyframes socialWidgetsInit{
    from{
        margin-top: -30px;
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}

@keyframes socialWidgetsInit{
    from{
        margin-top: -30px;
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}

@-webkit-keyframes floatMenu {
	0% {
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
	50% {
		-webkit-transform: translatey(5px);
		        transform: translatey(5px);
	}
	100% {
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
}

@keyframes floatMenu {
	0% {
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
	50% {
		-webkit-transform: translatey(5px);
		        transform: translatey(5px);
	}
	100% {
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
}
@-webkit-keyframes floatMenuLink {
	0% {
		-webkit-transform: translatex(0px);
		        transform: translatex(0px);
	}
	100% {
		-webkit-transform: translatex(5px);
		        transform: translatex(5px);
    }
}
@keyframes floatMenuLink {
	0% {
		-webkit-transform: translatex(0px);
		        transform: translatex(0px);
	}
	100% {
		-webkit-transform: translatex(5px);
		        transform: translatex(5px);
    }
}



