/* ABOUT ME PAGE */
.aboutMePage {
    height: 350px;
    color: yellow;
    position: relative;
    -webkit-perspective: 450px;
            perspective: 450px;
    font-size: 2vw;
    width: 1000px;
    margin: 5px;

}

.aboutMePage h2 {
    position: fixed;
    z-index: 1;
    cursor:default;
    font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    text-decoration:underline;
}

.aboutMePage h2:hover ~ .aboutMeText{
    transition: 1s;
    display: block;
    -webkit-animation: crawlText 230s linear forwards;
            animation: crawlText 230s linear forwards;
}
.aboutMePage h2:hover ~ .crawlTitle{
    display: none;
}

.aboutMeText h1 {
    text-align: center;
    font-family: 'Starjedi';
}

.aboutMePage p {
    font-family: Century Gothic, CenturyGothic, AppleGothic, sans-serif;
}
.crawlTitle{
    font-family: 'Starjedi';
    color: black;
    text-shadow: -1px -1px yellow, 1px -1px yellow, -1px 1px yellow, 1px 1px yellow;
    font-size: 150px;
}
.aboutMeText {
    transition: 1s;
    display: none;
    z-index: -1;
    text-align: justify;
    padding-left: 5px;
    /* animation: crawlText 100s linear forwards; */

}

@-webkit-keyframes crawlText {
    0% {
        top: 0;
        -webkit-transform: translateY(170px) translateZ(450px) rotateX(40deg);
                transform: translateY(170px) translateZ(450px) rotateX(40deg);
    }

    97% {
        top: -7500px;
        -webkit-transform: translateY(-2000px) translateZ(-2500px) rotateX(70deg);
                transform: translateY(-2000px) translateZ(-2500px) rotateX(70deg);
    }

    100% {
        opacity: 0;
    }
    
}

@keyframes crawlText {
    0% {
        top: 0;
        -webkit-transform: translateY(170px) translateZ(450px) rotateX(40deg);
                transform: translateY(170px) translateZ(450px) rotateX(40deg);
    }

    97% {
        top: -7500px;
        -webkit-transform: translateY(-2000px) translateZ(-2500px) rotateX(70deg);
                transform: translateY(-2000px) translateZ(-2500px) rotateX(70deg);
    }

    100% {
        opacity: 0;
    }
    
}


